import React, { useState, useEffect } from 'react';
import Layout from '@/components/Layout';
import { Table, InputGroup, FormControl } from 'react-bootstrap';
import PaginationComponent from '@/components/PaginationComponent'; // Import the Pagination Component
import styles from '../styles/order.module.css';

const OrderPage = () => {
  const [orders, setOrders] = useState([]);
  const [filteredOrders, setFilteredOrders] = useState([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(10);

  // Simulate fetching data from an API
  useEffect(() => {
    const fetchOrders = async () => {
      const fetchedOrders = [
        { id: 12345, customer: "John Doe", date: "2024-12-28", status: "Pending" },
        { id: 12346, customer: "Jane Smith", date: "2024-12-27", status: "Completed" },
        { id: 12347, customer: "Alice Brown", date: "2024-12-26", status: "Cancelled" },
        { id: 12348, customer: "Bob White", date: "2024-12-25", status: "Pending" },
        { id: 12349, customer: "Charlie Green", date: "2024-12-24", status: "Completed" },
        // Add more dummy orders as needed
      ];
      setOrders(fetchedOrders);
      setFilteredOrders(fetchedOrders);
    };
    fetchOrders();
  }, []);

  // Paginate filtered orders
  const paginatedOrders = filteredOrders.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  // Handle page change
  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  // Handle items per page change
  const handleItemsPerPageChange = (size) => {
    setItemsPerPage(size);
    setCurrentPage(1); // Reset to the first page when the page size changes
  };

  // Handle search
  const handleSearch = (event) => {
    const query = event.target.value.toLowerCase();
    setSearchQuery(query);

    const filtered = orders.filter(
      (order) =>
        order.customer.toLowerCase().includes(query) ||
        order.id.toString().includes(query)
    );
    setFilteredOrders(filtered);
    setCurrentPage(1); // Reset to the first page when the search changes
  };

  return (
    <Layout>
      <div className="container mt-5">
        <h1 className={`${styles.pageTitle} text-center mb-4`}>Order Management</h1>

        {/* Search */}
        <div className="row mb-4">
          <div className="col-12 col-md-6">
            <InputGroup>
              <FormControl
                placeholder="Search by customer name or order ID"
                value={searchQuery}
                onChange={handleSearch}
              />
            </InputGroup>
          </div>
        </div>

        {/* Table */}
        <div className="table-responsive">
          <Table striped bordered hover className={styles.table}>
            <thead className={styles.tableHeader}>
              <tr>
                <th>Order ID</th>
                <th>Customer Name</th>
                <th>Date of Order</th>
                <th>Status</th>
              </tr>
            </thead>
            <tbody>
              {paginatedOrders.map((order) => (
                <tr key={order.id}>
                  <td>#{order.id}</td>
                  <td>{order.customer}</td>
                  <td>{order.date}</td>
                  <td>{order.status}</td>
                </tr>
              ))}
            </tbody>
          </Table>
        </div>

        {/* Pagination */}
        <PaginationComponent
          totalItems={filteredOrders.length}
          itemsPerPage={itemsPerPage}
          currentPage={currentPage}
          onPageChange={handlePageChange}
          onItemsPerPageChange={handleItemsPerPageChange}
        />
      </div>
    </Layout>
  );
};

export default OrderPage;
